let resizeFunc = (e) => {

  let originalDimen = {
    height: 1800,
    width: 1920
  }

  let pageDimen = {
    height: window.innerHeight,
    width: window.innerWidth
  };

  let logoDimen = {
    height: 1100,
    width: 450
  };

  let backgroundEle = document.getElementById("dynbackground");
  let multiplier;
  if (pageDimen.height > pageDimen.width) {
    if (pageDimen.width < logoDimen.width)
      multiplier = pageDimen.width / logoDimen.width;
    else
      multiplier = logoDimen.width / pageDimen.width;

    backgroundEle.style.height = "auto";
    if (logoDimen.height / originalDimen.height * backgroundEle.height > pageDimen.height) {
      backgroundEle.style.width = "auto";
      backgroundEle.style.height = originalDimen.height * pageDimen.height / logoDimen.height - 20 + "px";
    } else
      backgroundEle.style.width = originalDimen.width * multiplier + "px";

  } else {
    if (pageDimen.height < logoDimen.height)
      multiplier = pageDimen.height / logoDimen.height;
    else
      multiplier = logoDimen.height / pageDimen.height;
    backgroundEle.style.width = "auto";
    if (logoDimen.width / originalDimen.width * backgroundEle.width > pageDimen.width) {
      backgroundEle.style.height = "auto";
      backgroundEle.style.width = originalDimen.width * pageDimen.width / logoDimen.width - 20 + "px";
    } else
      backgroundEle.style.height = originalDimen.height * multiplier + "px";
  }
  let offset = (pageDimen.width - backgroundEle.width) / 2;
  backgroundEle.style.left = offset+ "px";
}

//window.addEventListener("resize", resizeFunc);

let open = false;
let toggleFunc = () => {
  let content = document.getElementsByClassName('content')[0];
  let overlay = document.getElementsByClassName('overlay')[0];
  let sidebar = document.getElementsByClassName("sidebar")[0];
  let main = document.getElementsByClassName("mainPage")[0];
  main.classList.toggle('active');
  overlay.classList.toggle('active');
  content.classList.toggle('active');
  sidebar.classList.toggle('active');
  if (open) {
    overlay.removeEventListener('click', toggleFunc);
  } else {
    overlay.addEventListener('click', toggleFunc);
  }
  open = !open;
}
let navButton = document.getElementsByClassName('hamburger')[0];
navButton.addEventListener('click', toggleFunc);
window.addEventListener("load", function(event) {
  resizeFunc();
});

resizeFunc();
